import http from '@/api/http.js'
export default{
	data(){
		return{
			cashList:[]
		}
	},
	onLoad() {
		this.getCash()
	},
	methods:{
		getCash(){
			let data = {
				token:uni.getStorageSync('token')
			}
			http('/api/User/getCouponList','get',data).then(res=>{
				if(res.data.status == '200'){
					this.cashList = res.data.data.couponList
				}else{
					this.$util.showToast(res.data.message)
					return
				}
				console.log(res)
			})
		},
		toMine(){
			// uni.switchTab({
			// 	url:'/pages/mine/mine'
			// })
			uni.navigateBack({
				delta:1
			})
		}
	}
}
